.search-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 16px 0;
    background-color: #2d22d3;
    box-shadow: 0 10px 8px -5px rgb(112 125 127 / 80%);
    z-index: 10;
}
.search-form {
    display: flex;
    height: 40px;
    width: max-content;
    margin: 0 auto;
    border: 1px solid #434343;
    border-radius: 4px;
    overflow: hidden;
}